<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :isGetingItems="isGetingItems"
  >
    <v-layout column class="lay-body">
      <span class="txt-top">다이브로이더가 <br />되어 볼까요?</span>
      <span class="txt-desc mt-7">로그인 정보를 입력해 주세요</span>
      <c-input
        :label="'이메일 주소'"
        :hint="'이메일 주소를 입력해주세요'"
        :value.sync="inputList.id.value"
        class="mt-8"
      />
      <c-input
        :label="'비밀번호'"
        :hint="'비밀번호를 입력해주세요'"
        :value.sync="inputList.pw.value"
        :type="'password'"
        class="mt-8"
        :errorMsg="errorCnt > 0 ? '비밀번호가 일치하지 않습니다.' : ''"
      />
      <c-btn
        class="mt-5"
        :height="48"
        :thema="`default-bg${
          !inputList.id.value || !inputList.pw.value ? '-disable' : ''
        }`"
        :fontWeight="400"
        :fontSize="16"
        :readOnly="!inputList.id.value || !inputList.pw.value"
        :onClick="onClickActionLogin"
        >다음</c-btn
      >
      <span
        class="txt-forget mt-2 text-center"
        @click.prevent.stop="pushRouter({ name: 'fpw' })"
        >비밀번호를 잊어버리셨나요?</span
      >
      <span class="txt-or text-center">or</span>
      <c-btn
        class="btn-sns"
        :height="44"
        :fontSize="15"
        :thema="`gray`"
        :fontWeight="700"
        :onClick="() => pushRouter({ name: 'join' })"
      >
        <span class="txt-sns email">이메일로 가입하기</span>
      </c-btn>
      <c-btn
        class="btn-sns"
        :height="44"
        :fontSize="16"
        :thema="`gray`"
        :fontWeight="400"
        :onClick="snsFaceLoginAction"
      >
        <img
          class="img-sns"
          :src="require('@/assets/images/ic-facebook.svg')"
        />
        페이스북으로 계속하기
      </c-btn>
      <c-btn
        class="btn-sns"
        :height="44"
        :fontSize="16"
        :thema="`gray`"
        :fontWeight="400"
        :onClick="snsGoogleLoginAction"
      >
        <img class="img-sns" :src="require('@/assets/images/ic-google.svg')" />
        구글로 계속하기
      </c-btn>
      <c-btn
        class="btn-sns"
        :height="44"
        :fontSize="16"
        :thema="`gray`"
        :fontWeight="400"
        :onClick="snsAppleLoginAction"
      >
        <img class="img-sns" :src="require('@/assets/images/ic-apple.svg')" />
        애플로 계속하기
      </c-btn>
    </v-layout>
  </lay-header>
</template>

<script>
export default {
  middleware: "guest",
  data() {
    return {
      isGetingItems: false,
      errorCnt: 0,
      inputList: {
        id: {
          value: '',
          errMsg: "",
          validation: () => {
            return this.checkId();
          },
        },
        pw: {
          value: "",
          errMsg: "",
          validation: () => {
            return this.checkPw();
          },
        },
      },
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    async onClickActionLogin() {
      for (let key in this.inputList) {
        if (this.inputList[key].validation) {
          if (!this.inputList[key].validation()) {
            return false;
          }
        }
      }
      if (this.isGetingItems) {
        return;
      }
      this.isGetingItems = true;
      this.$store.dispatch('Login', { 
          loginId: this.inputList.id.value, 
          password: this.inputList.pw.value
      }).then(() => {
        this.isGetingItems = false;
        this.pushRouter({ name: "device" });
        this.errorCnt = 0;
      }).catch((err) => {
        this.isGetingItems = false;
        this.errorCnt++;
        this.showToast(err);
      })

    },
    checkId() {
      if (
        !this.inputList.id.value ||
        !this.validateEmail(this.inputList.id.value)
      ) {
        this.showToast("이메일 주소를 정확히 입력해주세요.");
        return false;
      }
      return true;
    },
    checkPw() {
      if (!this.inputList.pw.value) {
        this.showToast("비밀번호를 입력해주세요.");
        return false;
      }
      return true;
    },
    snsFaceLoginAction() {
      window.location.href = "/face/login";
    },
    snsGoogleLoginAction() {
      window.location.href = "/google/login";
    },
    snsAppleLoginAction() {
      window.location.href = "/apple/login";
    },
  },
  beforeRouteEnter(to, from, next) {
    next();
  },
};
</script>

